/* General App Styling */

/* NavBar Styling */

.navbar {
    box-shadow: 0 2px 4px 0 rgba(38, 47, 42, 1.0), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
    border: 0px solid black;
    background: linear-gradient(180deg, lightgray, white 21%, lightgray 69%);
}

#navBarLogoText {
    height: 80px;
    min-height: 80px;
    padding: 5px 5px 5px 10px;
}

.navBarAppHeader {
    position: absolute;
    right: 20px;
}

#navBarAppText {
    color: white;
    font-family: 'Roboto', sans-serif;
    text-shadow: 1px 1px #262f2a;
}
/* Add New Building Page Styling */

#buildingDisplayContainer {
    margin: 20px;
}

#buildingDisplayHeader {
    font-size: 15px;
    margin-bottom: 4px;
}

#buildingDisplayHr {
    margin-bottom: 15px;
    margin-top: 0;
}

#buildingDisplay {
    text-align: center;
}

.buildingDisplayBtn {
    background-color: lightskyblue; /* #00467f; */
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 15px;
    margin: 0 10px 10px 0;
    outline: none;
    padding: 3px 20px;
    min-width: 260px;
}
  
.buildingDisplayBtn:hover {
    background-color: #00547f;
}
  
.buildingDisplayBtn:focus {
    outline: none;
}
