/* Dashboard Page Styling */

.dashDataContainer {
    border-radius: 4px;
    margin: 10px 20px;
    min-height: 200px;
    max-height: 400px;
    overflow: auto;
    text-shadow: 0px 0px;
}

.dashBldgContainer {
    background-color: white;
    border-radius: 4px;
    margin-bottom: 40px;
    padding: 0 10px 10px;
    text-shadow: 0px 0px;
}

.dashBldgName {
    background: linear-gradient(180deg, lightgray, white 21%, lightgray 69%);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    font-size: 18px;
    margin: 0 -10px;
    padding: 5px 10px;
}

.dashBldgName a {
    border-radius: 4px;
    color: rgb(0 70 127);
    padding: 2px 5px;
    text-decoration: none;
    text-shadow: 1px 1px white;
}

.dashBldgName a:hover {
    color: lightsalmon;
    text-decoration: none;
}

.dashBldgName a:active {
    text-decoration: none;
}

.dashDataHr {
    margin-top: 5px;
}

.dashFloorContainer {
    font-family: monospace;
    margin: 20px 15px 10px;
    text-shadow: 0px 0px;
}

.dashFloorName {
    background-color: lightskyblue;
    border-radius: 4px;
    font-size: 14px;
}

.dashFloorName a {
    border-radius: 4px;
    color: rgb(0 70 127);
    padding: 0 10px;
    text-decoration: none;
}

.dashFloorName a:hover {
    color: white; /* rgb(21 140 186); */
    text-decoration: none;
}

.dashFloorName a:active {
    text-decoration: none;
}

.dashRoomContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 10px 20px;
}

.dashRoomName {
    background-color: rgb(0 70 127);
    border-radius: 4px;
    font-size: 13px;
    margin: 5px;
    min-width: 12%;
    padding: 0 7px;
    text-align: center;
}

.dashRoomName a {
    color: white;
}

.dashRoomName:hover {
    background-color: lightslategray;
}
/* Login Page Styling */

#loginFormHeader { 
    margin: 20px 0;
}
/* General App Styling */

body {
    background-color: lightslategray;
    color: white;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    text-shadow: 1px 1px #00467f;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
    color: white;
}

hr {
    border-width: 2px;
}

/* General Form Styling */

.genFormSettings {
    margin: 20px;
}

.form-control {
    border-radius: 4px;
    box-shadow: unset;
    font-size: 15px;
    height: 27px;
    padding: 5px 7px;
}

label {
    font-size: 15px;
    margin-bottom: 3px;
}

.capitalized {
    text-transform: capitalize;
}

.selectStyling {
    height: 27px !important;
    padding: 3px;
}

select:invalid {
    color: #999 !important;
}

option:disabled {
    color: #999 !important;
}


/* General Button Styling */
  
.genAppBtn {
    background-color: #00467f;
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 15px;
    margin-top: 10px;
    outline: none;
    padding: 10px 20px;
    min-width: 125px;
}
  
.genAppBtn:hover {
    background-color: #00547f;
}
  
.genAppBtn:focus {
    outline: none;
}

/* Navigation Styling */

.pageHeaderText {
    float: left;
}

.pageHeaderNavBtnContainer {
    float: right;
}

.pageHeaderNavBtn {
    background-color: lightskyblue;
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 15px;
    margin: 17px 0 0 10px;
    outline: none;
    padding: 5px 10px;
}

.pageHeaderNavBtn:hover {
    background-color: #00547f;
}
  
.pageHeaderNavBtn:focus {
    outline: none;
}

.pageHeaderHr {
    overflow: auto;
    clear: both;
}
